* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #fff;
  user-select: none;
  font-family: "Barlow", sans-serif, Roboto;
  font-weight: 500;
  overflow-x: hidden;
  scrollbar-color: #B4977B #E5E5E5;
  scrollbar-width: thin;
}
body::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
body::-webkit-scrollbar-thumb {
  background: #B4977B;
  border-radius: 2px;
}
body::-webkit-scrollbar-track {
  background: #E5E5E5;
  border-radius: 2px;
}

.pages {
  display: none;
}
.pages--active {
  display: block;
}

.pages__active__animTop__To__Bottom {
  animation: pages_active2 1s;
}

@keyframes pages_active2 {
  0% {
    opacity: 1;
    transform: translate(0, 0);
  }
  35% {
    transform: translate(0, 10vh);
    opacity: 0;
  }
  50% {
    opacity: 0;
    transform: translate(0, -100vh);
  }
}
.container {
  display: flex;
  width: 100%;
  height: 100vh;
}
.container__rowLeft {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 57px;
  padding-bottom: 100px;
  width: 294px;
  height: 100%;
  border-right: 2px solid #E5E5E5;
}
.container__rowLeft__logo {
  color: #050215;
  font-size: 30px;
}
.container__rowRight {
  width: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
  scrollbar-color: #B4977B #E5E5E5;
  scrollbar-width: thin;
  margin-right: 1px;
}
.container__rowRight__row {
  display: flex;
}
.container__rowRight::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.container__rowRight::-webkit-scrollbar-thumb {
  background: #B4977B;
  border-radius: 2px;
}
.container__rowRight::-webkit-scrollbar-track {
  background: #E5E5E5;
  border-radius: 2px;
}

.pagination {
  flex: 1;
  margin: 15px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.pagination__numbers {
  font-size: 54px;
  color: #B4977B;
}
.pagination__cont {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 4px;
  height: 200px;
  border-radius: 2px;
  background-color: #E5E5E5;
  position: relative;
  transition: 250ms;
  cursor: pointer;
}
.pagination__cont__items {
  width: 100%;
  flex: 1;
  transition: 0.3s;
}
.pagination__cont__items--active {
  background-color: #B4977B;
}
.pagination__cont:hover {
  width: 10px;
}

.btnImgHolder__next,
.btnImgHolder__prev {
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}
.btnImgHolder__next:active,
.btnImgHolder__prev:active {
  transform: scale(0.98);
}

.btnImgHolder__prev {
  display: none;
  margin-bottom: 5px;
  transform: rotate(180deg);
}
.btnImgHolder__prev:active {
  transform: scale(0.98) rotate(180deg);
}

.btnImgHolder__img {
  display: block;
  max-width: 58px;
  object-fit: contain;
}

.rightInnerColumn {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  padding: 0 10px;
  min-width: 300px;
}

.net__icon {
  position: absolute;
}
.net__icon__1 {
  width: 160px;
  height: 241px;
  top: 234px;
  right: -80px;
}
.net__icon__2 {
  width: 130px;
  height: 195px;
  bottom: -160px;
  left: 60%;
}
.net__icon__3 {
  width: 160px;
  height: 241px;
  right: -49%;
  top: 95px;
}

.bannerImgHolder {
  position: relative;
}

.bannerImg {
  display: block;
  width: 100%;
  object-fit: contain;
}

.ImageAndSocial {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 517px;
  height: 100%;
}

.rightInnerColumnInnerBottom {
  width: 85%;
  min-height: 280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 40px;
}

.socialRow {
  position: relative;
  width: 41%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 10px;
  align-items: flex-end;
  min-height: 163px;
}

.socialRowColumn {
  flex: 0 0 auto;
  font-size: 14px;
  line-height: 17px;
  color: #050215;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.2s;
}
.socialRowColumn:hover {
  color: #B4977B;
}

.bannerImgText {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 117px;
  font-size: 28px;
  color: #fff;
  background-color: #E5E5E5;
  background: rgba(255, 255, 255, 0.3019607843);
  backdrop-filter: blur(10px);
}

.rightInnerColumnInner {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 650px;
  padding-bottom: 63px;
}

.rightInnerColumnSection {
  position: relative;
  margin-top: 160px;
  width: 100%;
  max-height: 490px;
}

.rightInnerColumnHeader {
  display: flex;
  align-items: center;
  padding-top: 64px;
  margin-bottom: 20px;
}

.rightInnerColumnHeaderColumn {
  position: relative;
  flex: 1;
  font-size: 18px;
  line-height: 22px;
  color: #050215;
  text-transform: capitalize;
  transition: color 240ms;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 6px;
  text-decoration: none;
}
.rightInnerColumnHeaderColumn__basket {
  display: flex;
  justify-content: right;
  overflow: inherit;
}
.rightInnerColumnHeaderColumn__basket img {
  max-width: 16px;
}
.rightInnerColumnHeaderColumn__basketCount {
  position: absolute;
  bottom: 16px;
  right: 0;
  width: 10px;
  height: 10px;
  background-color: #050215;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  font-style: normal;
  font-weight: 700;
  font-size: 6px;
}
.rightInnerColumnHeaderColumn:hover {
  color: #B4977B;
}

.mibileNavigation {
  display: none;
}

.rightInnerTitle {
  font-weight: 600;
  font-size: 64px;
  line-height: 77px;
  color: #050215;
  margin-bottom: 40px;
}

.textContentHolder {
  width: 100%;
  padding-right: 20px;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 290px;
  font-weight: 400;
  font-size: 18px;
  word-break: break-word;
  line-height: 32px;
  color: rgba(5, 2, 21, 0.75);
  margin-bottom: 38px;
  scrollbar-color: #B4977B #E5E5E5;
  scrollbar-width: thin;
}
.textContentHolder::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.textContentHolder::-webkit-scrollbar-thumb {
  background: #B4977B;
  border-radius: 2px;
}
.textContentHolder::-webkit-scrollbar-track {
  background: #E5E5E5;
  border-radius: 2px;
}

.shoppingNowButton {
  outline: none;
  border: none;
  background: #B4977B;
  color: #fff;
  font-size: 18px;
  padding: 0 10px;
  text-transform: capitalize;
  height: 60px;
  min-width: 178px;
  border-radius: 50px;
  transition: 0.2s;
  cursor: pointer;
}
.shoppingNowButton:hover {
  opacity: 0.9;
}
.shoppingNowButton:active {
  transform: scale(0.98);
}

.pageNumber {
  text-align: right;
  font-size: 200px;
  font-weight: 600;
  color: white;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #B4977B;
}

.mibileNavigation {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0, -300px);
  opacity: 0.5;
  transition: 400ms ease-in-out;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  z-index: 10;
  width: 100%;
}
.mibileNavigation--active {
  transform: translate(0, 0);
  opacity: 1;
}
.mibileNavigation__wrapper {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  width: 40%;
}

.menuBtnMobile {
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 35px;
  z-index: 11;
}

@media screen and (max-width: 2050px) {
  .rightInnerColumnSection {
    margin-top: 50px;
  }
  .container__rowRight__row {
    height: 700px;
  }
}
@media screen and (max-width: 1300px) {
  .rightInnerTitle {
    font-size: 40px;
    line-height: normal;
  }
  .rightInnerColumnHeader {
    justify-content: space-around;
  }
  .rightInnerColumnHeaderColumn {
    flex: none;
  }
}
@media screen and (max-width: 1000px) {
  .container__rowRight__row {
    height: auto;
    flex-direction: column-reverse;
  }
  .rightInnerColumn {
    align-items: center;
  }
  .rightInnerColumnSection {
    text-align: center;
  }
  .container__rowRight {
    overflow-y: inherit;
  }
}
@media screen and (max-width: 1000px) {
  .socialRow {
    width: 100%;
    min-height: 50px;
  }
  .rightInnerColumnInnerBottom {
    min-height: auto;
    flex-direction: column;
  }
}
@media screen and (max-width: 625px) {
  .container__rowLeft {
    width: 130px;
  }
  .rightInnerColumnHeader {
    display: none;
  }
  .rightInnerColumn {
    padding: 0;
  }
  .rightInnerColumnHeaderColumn__basket {
    margin-top: 15px;
    position: relative;
    width: fit-content;
    justify-content: center;
  }
  .rightInnerColumnHeaderColumn {
    line-height: 2;
  }
  .menuBtnMobile {
    display: block;
  }
  .mibileNavigation {
    display: flex;
  }
}
@media screen and (max-width: 500px) {
  .container__rowLeft__logo {
    font-size: 26px;
  }
  .pagination__numbers {
    font-size: 25px;
  }
  .btnImgHolder__Img {
    width: 40px;
  }
  .rightInnerTitle {
    font-size: 30px;
  }
  .textContentHolder {
    font-size: 15px;
    line-height: inherit;
  }
  .shoppingNowButton {
    height: 45px;
    min-width: 130px;
    font-size: 15px;
  }
  .bannerImgText {
    font-size: 20px;
  }
}