$white: #fff;
$Jaguar_Color: #050215;
$Pale_Taupe: #B4977B;
$font_Barlow: "Barlow", sans-serif, Roboto;
$Mercury_color: #E5E5E5;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: $white;
  user-select: none;
  font-family: $font_Barlow;
  font-weight: 500;
  overflow-x: hidden;
  scrollbar-color: $Pale_Taupe $Mercury_color;
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: $Pale_Taupe;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $Mercury_color;
    border-radius: 2px;
  }
}

.pages {
  display: none;

  &--active {
    display: block;
  }
}

.pages__active__animTop__To__Bottom {
  animation: pages_active2 1s;
}

@keyframes pages_active2 {
  0% {
    opacity: 1;
    transform: translate(0, 0);
  }

  35% {
    transform: translate(0, 10vh);
    opacity: 0;
  }

  50% {
    opacity: 0;
    transform: translate(0, -100vh);
  }
}

.container {
  display: flex;
  width: 100%;
  height: 100vh;

  &__rowLeft {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 57px;
    padding-bottom: 100px;
    width: 294px;
    height: 100%;
    border-right: 2px solid $Mercury_color;

    &__logo {
      color: $Jaguar_Color;
      font-size: 30px;
    }
  }

  &__rowRight {
    width: 100%;
    overflow-y: hidden;
    overflow-x: hidden;
    scrollbar-color: $Pale_Taupe $Mercury_color;
    scrollbar-width: thin;
    margin-right: 1px;

    &__row {
      display: flex;
    }

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: $Pale_Taupe;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-track {
      background: $Mercury_color;
      border-radius: 2px;
    }
  }
}

.pagination {
  flex: 1;
  margin: 15px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  position: relative;

  &__numbers {
    font-size: 54px;
    color: $Pale_Taupe;
  }

  &__cont {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 4px;
    height: 200px;
    border-radius: 2px;
    background-color: $Mercury_color;
    position: relative;
    transition: 250ms;
    cursor: pointer;

    &__items {
      width: 100%;
      flex: 1;
      transition: 0.3s;

      &--active {
        background-color: $Pale_Taupe;
      }
    }

    &:hover {
      width: 10px;
    }
  }
}

.btnImgHolder__next,
.btnImgHolder__prev {
  outline: none;
  border: none;
  background: none;
  cursor: pointer;

  &:active {
    transform: scale(0.98);
  }
}

.btnImgHolder__prev {
  display: none;
  margin-bottom: 5px;
  transform: rotate(180deg);

  &:active {
    transform: scale(0.98) rotate(180deg);
  }
}

.btnImgHolder__img {
  display: block;
  max-width: 58px;
  object-fit: contain;
}

.rightInnerColumn {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  padding: 0 10px;
  min-width: 300px;
}

.net__icon {
  position: absolute;

  &__1 {
    width: 160px;
    height: 241px;
    top: 234px;
    right: -80px;
  }

  &__2 {
    width: 130px;
    height: 195px;
    bottom: -160px;
    left: 60%;
  }

  &__3 {
    width: 160px;
    height: 241px;
    right: -49%;
    top: 95px;
  }
}

.bannerImgHolder {
  position: relative;
}

.bannerImg {
  display: block;
  width: 100%;
  object-fit: contain;
}

.ImageAndSocial {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 517px;
  height: 100%;
}

.rightInnerColumnInnerBottom {
  width: 85%;
  min-height: 280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  // overflow: hidden;
  padding-bottom: 40px;
}

.socialRow {
  position: relative;
  width: 41%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 10px;
  align-items: flex-end;
  min-height: 163px;
}

.socialRowColumn {
  flex: 0 0 auto;
  font-size: 14px;
  line-height: 17px;
  color: $Jaguar_Color;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.2s;

  &:hover {
    color: $Pale_Taupe;
  }
}

.bannerImgText {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 117px;
  font-size: 28px;
  color: $white;
  background-color: $Mercury_color;
  background: #ffffff4d;
  backdrop-filter: blur(10px);
}

.rightInnerColumnInner {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 650px;
  padding-bottom: 63px;
}

.rightInnerColumnSection {
  position: relative;
  margin-top: 160px;
  width: 100%;
  max-height: 490px;
}

.rightInnerColumnHeader {
  display: flex;
  align-items: center;
  padding-top: 64px;
  margin-bottom: 20px;
}

.rightInnerColumnHeaderColumn {
  position: relative;
  flex: 1;
  font-size: 18px;
  line-height: 22px;
  color: $Jaguar_Color;
  text-transform: capitalize;
  transition: color 240ms;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 6px;
  text-decoration: none;

  &__basket {
    display: flex;
    justify-content: right;
    overflow: inherit;

    img {
      max-width: 16px;
    }
  }

  &__basketCount {
    position: absolute;
    bottom: 16px;
    right: 0;
    width: 10px;
    height: 10px;
    background-color: #050215;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    font-style: normal;
    font-weight: 700;
    font-size: 6px;
  }

  &:hover {
    color: $Pale_Taupe;
  }
}

.mibileNavigation {
  display: none;
}

.rightInnerTitle {
  font-weight: 600;
  font-size: 64px;
  line-height: 77px;
  color: $Jaguar_Color;
  margin-bottom: 40px;
}

.textContentHolder {
  width: 100%;
  padding-right: 20px;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 290px;
  font-weight: 400;
  font-size: 18px;
  word-break: break-word;
  line-height: 32px;
  color: rgba(5, 2, 21, 0.75);
  margin-bottom: 38px;
  scrollbar-color: $Pale_Taupe $Mercury_color;
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: $Pale_Taupe;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $Mercury_color;
    border-radius: 2px;
  }
}

.shoppingNowButton {
  outline: none;
  border: none;
  background: $Pale_Taupe;
  color: $white;
  font-size: 18px;
  padding: 0 10px;
  text-transform: capitalize;
  height: 60px;
  min-width: 178px;
  border-radius: 50px;
  transition: 0.2s;
  cursor: pointer;

  &:hover {
    opacity: 0.9;
  }

  &:active {
    transform: scale(0.98);
  }
}

.pageNumber {
  text-align: right;
  font-size: 200px;
  font-weight: 600;
  color: white;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: $Pale_Taupe;
}

.mibileNavigation {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0, -300px);
  opacity: 0.5;
  transition: 400ms ease-in-out;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  z-index: 10;
  width: 100%;

  &--active {
    transform: translate(0, 0);
    opacity: 1;
  }

  &__wrapper {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    width: 40%;
  }
}


.menuBtnMobile {
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 35px;
  z-index: 11;
}

@media screen and (max-width:2050px) {
  .rightInnerColumnSection {
    margin-top: 50px;
  }

  .container__rowRight__row {
    height: 700px;
  }
}

@media screen and (max-width:1300px) {

  .rightInnerTitle {
    font-size: 40px;
    line-height: normal;
  }

  .rightInnerColumnHeader {
    justify-content: space-around;
  }

  .rightInnerColumnHeaderColumn {
    flex: none;
  }
}

@media screen and (max-width:1000px) {
  .container__rowRight__row {
    height: auto;
    flex-direction: column-reverse;
  }

  .rightInnerColumn {
    align-items: center;
  }

  .rightInnerColumnSection {
    text-align: center;
  }

  .container__rowRight {
    overflow-y: inherit;
  }
}

@media screen and (max-width:1000px) {
  .socialRow {
    width: 100%;
    min-height: 50px;
  }

  .rightInnerColumnInnerBottom {
    min-height: auto;
    flex-direction: column;
  }
}

@media screen and (max-width:625px) {
  .container__rowLeft {
    width: 130px;
  }

  .rightInnerColumnHeader {
    display: none;
  }

  .rightInnerColumn {
    padding: 0;
  }

  .rightInnerColumnHeaderColumn__basket {
    margin-top: 15px;
    position: relative;
    width: fit-content;
    justify-content: center;
  }

  .rightInnerColumnHeaderColumn {
    line-height: 2;
  }

  .menuBtnMobile {
    display: block;
  }

  .mibileNavigation {
    display: flex;
  }
}

@media screen and (max-width:500px) {
  .container__rowLeft__logo {
    font-size: 26px;
  }

  .pagination__numbers {
    font-size: 25px;
  }

  .btnImgHolder__Img {
    width: 40px;
  }

  .rightInnerTitle {
    font-size: 30px;
  }

  .textContentHolder {
    font-size: 15px;
    line-height: inherit;
  }

  .shoppingNowButton {
    height: 45px;
    min-width: 130px;
    font-size: 15px;
  }

  .bannerImgText {
    font-size: 20px;
  }
}